@import url(https://fonts.googleapis.com/css2?family=Karla&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Karla', sans-serif;
}

.no-fill {
  border: none;
  background: none;
}

.fill {
  border: none;
  background: black;
  color: white;
}

.underline {
  border-bottom: 1px solid black;
  padding: 0px 6px;
}

.separate-line {
  background: black;
  opacity: 0.3;
  width: 2px;
  height: 30px;
}

.smaller-font {
  font-size: 11px;
}

.object-position-left {
  object-position: 0px -30px;
}
.object-position-right {
  object-position: -280px -30px;
}
.object-position-center {
  object-position: -140px 0px;
}

.box-shadow {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.05),
    0 6px 20px 0 rgba(129, 74, 74, 0.05);
}

.card-image {
  width: 100%;
  height: 180px;
  object-position: center;
}

.card-holder {
  background: #fdfdfd;
}

.round-corner {
  border-radius: 20px;
}
* {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.travel-website-primary-color {
  color: #00928c;
}

.travel-website-primary-background {
  background-color: #00928c;
}

.travel-website-secondary-background {
  background-color: #eff9f8;
}

.box-shadow {
  box-shadow: 0px 15px 40px 0px rgba(180, 180, 180, 0.3);
}

/* navbar */
#travel-website-navItem:first-child {
  color: #00928c;
}

.travel-website-nav-button {
  border: none;
  border-radius: 40px;
  padding: 10px 20px;
}

/* homepage */
.home-window-height {
  height: 90.5vh;
}

.search-group {
  border-radius: 40px;
  border: 1px solid rgba(185, 185, 185, 0.3);
  padding: 8px;
}

.search-input {
  border: none;
  padding: 0px 6px;
}

/* About page */

.circle-image {
  border-radius: 50%;
  border: 1px solid white;
  width: 65px;
  height: 65px;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.circle-image:nth-child(n + 2) {
  position: absolute;
}

/* TourPackage page */

.circle-arrow {
  color: #00928c;
}

.travel-card-radius {
  border-radius: 10px;
}

.travel-card-group {
  position: relative;
}

.travel-place{
  height: 200px;
  width: 310px;
  object-position: center;
}

.travel-details {
  padding: 20px 30px;
  width: 300px;
  margin-top: -45px;
  margin-left: 50px;
  background: rgba(255, 255, 255, 0.9);
}

.button-outline-primary {
  border: 1px solid #00928c;
  background: none;
  border-radius: 25px;
  padding: 3px 20px;
}

.star {
  color: #f4ac4c;
}


/* contact us */
.contact-icon {
  border: 1px solid #00928d2a;
  border-radius: 100%;
  width: 100px;
  height: 100px;
}

.icon-margin {
  margin-top: -8px;
}

/* footer */
.subscribe-input {
  border: none;
}

.subscribe-input:focus {
  outline: none;
}

.subscribe-button {
  border-radius: 40px;
  border: none;
}

.subscribe-group {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 15px 20px;
  border-radius: 50px;
}

.subscribe-container {
  border-radius: 20px;
  height: 45%;
}

.footer-container {
  height: 40%;
}

.footer-container > * a {
  text-decoration: none;
  color: #00928c;
}


.footer {
  overflow: hidden;
  padding: 10px;
  border-top: 1px solid black;
}

.freuencyUI-nav-link {
  color: white;
}

.freuencyUI-nav-link:hover {
  color: #f9d802;
}

.backgroundImage {
  background-image: url(/nepa-design.github.io/static/media/girlWithHeadphone.843ebdf7.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: rgba(158, 58, 0, 0.2);
  background-blend-mode: saturation;
}

.circle-indicator {
  position: unset;
  border-radius: 50%;
  height: 20px !important;
  width: 20px !important;
}

.carousel-indicators {
  position: absolute;
  left: 0;
  margin-left: 5% !important;
  margin-right: 0px !important;
  margin-top: 0px !important;
  margin-bottom: 0px !important;
  -webkit-transform: translate(0, 55vh);
          transform: translate(0, 55vh);
  -webkit-flex-direction: column;
          flex-direction: column;
  grid-gap: 25px;
  gap: 25px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.carousel-inner {
  position: absolute !important;
  top: 0;
  left: 0;
  -webkit-transform: translate(20%, 25vh);
          transform: translate(20%, 25vh);
  width: 54%;
}

.carousel-container {
  position: relative !important;
}

.carousel-icon {
  border: none;
  height: 55px;
  width: 55px;
  border-radius: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: rgba(255, 255, 255, 0.6) !important;
  opacity: 1;
}

.carousel-icon:hover {
  background-color: #f9d802 !important;
}

.carousel-icon-group {
  height: 100px !important;
  width: 140px !important;
  display: -webkit-flex !important;
  display: flex !important;
  -webkit-flex-direction: row !important;
          flex-direction: row !important;
  position: absolute !important;
  top: 0;
  left: 0;
  -webkit-transform: translate(70vw, 40vh);
          transform: translate(70vw, 40vh);
  z-index: 5;
}

.carousel-content-button {
  border: none;
  background-color: #f9d802 !important;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px 34px;
}

.socialmedia-icon-group {
  position: absolute;
  right: 0px;
  -webkit-transform: translate(-5vw, 30vh);
          transform: translate(-5vw, 30vh);
}

.railroad-primary-color {
  color: #0e283d;
}

.railroad-primary-background {
  background-color: #012e4c;
}

.railroad-secondary-color {
  color: #0460a0;
}

.railroad-secondary-background {
  background-color: #0460a0;
}

.railroad-footer-background {
  background-color: #031d32;
}

.railroad-nav-items:hover, .railroad-header-items:hover {
    color: #4797ff;
}

/* header */

.separator {
  height: 80%;
  width: 2px;
  background-color: #104368;
}

.header-font-size {
  font-size: 12px;
}

/* navbar */
.btn {
  font-size: 14px;
  padding: 4px 15px;
  border-radius: 0px;
  font-weight: 600;
}

.nav-button-outline {
  border: 2px solid #104368;
}

.nav-railroad-button {
  background-color: #a31415;
  color: white;
}

.search-icon {
  font-size: 20px;
  margin-right: 10px;
}

.button-icon {
  font-size: 15px;
}

.nav-items {
  font-weight: 700;
  font-size: 14px;
}

/* home */
.background-image {
  background-image: url(/nepa-design.github.io/static/media/trainInBridge.95c73f58.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: rgba(0, 75, 124, 0.3);
  background-blend-mode: multiply;
  height: 77vh;
  width: 100%;
}

.search-bar {
  height: 12.1vh;
  background-image: url(/nepa-design.github.io/static/media/texture.d62c01e1.jpg);
  background-position: center;
  background-size: contain;
  background-repeat: repeat;
  background-color: rgba(0, 82, 136, 0.8);
  background-blend-mode: overlay;
}

.search-button {
  background-color: white;
  font-weight: 700;
}

.body {
  margin: 0;
  padding: 0;
}

.window-height {
  height: 100vh;
}

.full-height {
  height: 100%;
}

.half-height {
  height: 50%;
}

.quarter-height {
  height: 80%;
}

.window-width {
  width: 100vw;
}

.full-width {
  width: 100%;
}

a {
  font-weight: 500;
  text-decoration: none;
}
